
.wrapper {
  display: -webkit-flex;
  display: flex;
  width: 100vw;
}

.content {
  width: 100vw;
}


nav.navbar {
  margin-bottom: 1em;
}

img.menu-icon {
  width: 1.5em;
  margin: .3em 1em .3em .3em;
  cursor: pointer;
}

:root {
    --sidebarWidth: 300px;
    --sidebarBackground: #404E67;
    --sidebarActiveLink: #F8877C;
    --sidebarLink: #FDFDFE;
}


#sidebar {
    width: 300px;
    width: var(--sidebarWidth);
    height: 100vh;


    top: 0;
    left: 0;
    z-index: 999;
    margin-left: calc(-1 * 300px);
    margin-left: calc(-1 * var(--sidebarWidth));

    background: #404E67;

    background: var(--sidebarBackground);
    color: #fff;
    transition: all 0.3s;
}

#sidebar h2.app-title {
    padding: .5em;
}

#sidebar.open {
    margin-left: 0;
}

li.sidebar-link {
    color: #FDFDFE;
    color: var(--sidebarLink);
    cursor: pointer;
    background-color: transparent;
    background-color: initial;
}

li.sidebar-link a.active {
    color: #F8877C;
    color: var(--sidebarActiveLink);
    background-color: transparent;
    background-color: initial;
    border: none;
}

tr.clickable-row {
    cursor: pointer;
}

a.icon-button {
    margin-right: .5em;
}

.form-buttons button:first-child {
    margin-left: auto;
    margin-right: .5em;
}


div.blocks-container {
    min-height: 2em;
}

div.card.block-card {
    margin-bottom: .5em;
}

.card-header a {
    display: -webkit-flex;
    display: flex;
    cursor: pointer;
}

.card-header a i.fa-caret-down {
    margin: auto 0 auto auto;
    transition-duration: 0.5s;
}

.card-header a i.fa-exclamation-triangle {
    margin-right: .5em;
    margin-top: .25em;
}


i.fa-caret-down.expanded {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}


a.back-button {
    margin-bottom: 1em;
}

div.container.publish-buttons {
    margin: 2em auto;
}

div.publish-buttons button {
    margin-bottom: .5em;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

